
.mint-swipe[data-v-91f36326] {
    overflow: hidden;
    position: relative;
    height: 50%;
}
.mint-swipe-items-wrap[data-v-91f36326] {
    position: relative;
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0);
    transform: translateZ(0)
}
.mint-swipe-items-wrap>div[data-v-91f36326] {
    float: left;
    position: absolute;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    width: 100%;
    height: 100%;
    display: none
}
.mint-swipe-items-wrap>div.is-active[data-v-91f36326] {
    display: block;
    -webkit-transform: none;
    transform: none;
}
.mint-swipe-indicators[data-v-91f36326] {
    position: absolute;
    bottom: 10px;
    left: 50%;
